:root {
    --color-gray: rgb(240, 240, 240);
  }

  .punch-beginner {
    position: relative;
    color: white;
    font-size: 2rem;
    font-weight: 600;
    top: 2%;
  }

  .content-container {
    position: relative;
    bottom: .2rem;
    padding: 0 2rem 0 1rem;
    display: grid;
    grid-template-columns: 2fr 2fr;
    align-items: center;
  }
  
  .navigation-wrapper {
    background: var(--color-gray);
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    margin-bottom: 20px;
  }
  
  .navigation {
    display: flex;
    margin-left: 10px;
    margin-right: 10px;
    padding-left: 10px;
    padding-right: 10px;
    background: var(--color-gray);
  }
  
  .s3-letter-holder {
    position: fixed;
    top: 1.3rem;
    left: 42.7vw;
    width: 90px;
    display: block;
    margin-left: auto !important;
    margin-right: auto !important;
  }
  
  .navigation-dropdown-logout .logout {
    color: white;
    text-decoration: none;
  }
  
  .dropbtn {
    position: relative;
    font-size: 18px;
    line-height: 20px;
    color: white;
    padding: 16px;
    border: none;
    cursor: pointer;
    background: transparent;
  }
  
  .dropbtn:hover #text {
    color: white;
  }
  
  .dropbtn:hover {
    background: rgba(0, 0, 0, .05);
  }
  
  .button-content:hover {
    color: white !important;
  }
  
  .drop-caret {
    position: absolute;
    bottom: 12px;
  }
  
  .nav-bar--dropdown {
    position: relative;
    display: inline-block;
    margin-left: auto;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  }
  
  .dropdown-content .link-logout {
    color: rgb(60, 60, 60) !important;
    background: var(--color-gray);
    padding: 12px 16px;
    text-decoration: none;
    position: relative;
    z-index: 100;
  }
  
  .link-logout:hover {
    color: rgb(50, 50, 50) !important;
    background: rgb(230, 230, 230);
  }
  
  .company-name {
    color: white;
    font-weight: 200;
    white-space: nowrap;
    font-size: 30px;
    letter-spacing: 1px;
    position: relative;
    top: 0rem;
  }
  
  .navbar {
    position: fixed;
    padding: 15px 10px; 
    background-image: linear-gradient(to right, rgb(255, 209, 220), rgb(240, 180, 195));
    height: 3rem;
    width: 100%;
    top: 0rem;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    border-bottom: 1px dotted gray;
    z-index: 1000;
    overflow: visible;
  }
  
  .navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
  }
  
  /*screen size 700px and up */
  @media (min-width: 900px) {
    .navigation-sp3-letters-wrapper {
      height: 53.05px;
      width: 140px;
  
    }
  
    .navigation-s3-letter {
      font-size: 72px;
      line-height: 44px;
    }
  
    .navigation-s3-digit {
      font-size: 30px;
    }
  }
  
  h4 {
    color: gray;
    font-size: 1rem;
    margin-bottom: 0;
  }
  
  .row .no-gutters {
    margin-top: 7.5%;
  }
  
  .plain-link,
  .plain-link:hover,
  .plain-link:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
  }
  
  .plain-link:hover {
    color: rgb(227, 227, 227);
    background: gray;
  }
  
  
  .breadcrumb-link {
    text-decoration: none;
  }
  
  /* .breadcrumb-link:hover {
    color: #0a58ca;
    text-decoration: none;
  } */
  
  /* a,
  a:hover,
  a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
  } */
  
  .sidebar-icon,
  span {
    display: inline-block;
  }
  
  /* ---------------------------------------------------
      SIDEBAR STYLE
  ----------------------------------------------------- */
  
  .wrapper {
    position: fixed;
    width: 91.66666666%;
    left: 8.33333%;
    top: 5rem;
    border-bottom: 1px dotted gray;
    border-right: 1px dotted gray;
    background-image: linear-gradient(to right, rgb(245, 245, 245), rgb(227, 227, 227));
    height: 2.55rem;
    z-index: 1;
  }
  
  /* This is for the havigation bread crumb display */
  .breadcrumb-display {
    position: relative;
    margin-top: 0.5rem;
    margin-left: 0.5rem;
  }
  
  #sidebar {
    position: fixed;
    top: 5rem;
    width: 8.33333333%;
    height: 100vh;
    background: linear-gradient(to bottom, rgb(245, 245, 245), rgb(227, 227, 227));
    color: gray;
    text-align: center;
    border-bottom: 1px dotted gray;
    border-right: 1px dotted gray;
  }
  
  .sidebar-copyright {
    width:  8.33333333%;
    color: hsl(240deg 3% 68%);
    font-size: 6pt;
  }
  
  #sidebar .sidebar-header h3,
  #sidebar .CTAs {
    display: none;
  }
  
  #sidebar .sidebar-header strong {
    display: block;
  }
  
  #sidebar ul {
    padding-top: 2.5rem;
  }
  
  #sidebar ul li a {
    text-align: left;
    border-top: 1px dotted gray;
  }
  
  #sidebar ul li a {
    text-align: center;
  }
  
  #sidebar ul li a .sidebar-icon {
    display: block;
    font-size: 1.8em;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5px;
  }
  
  #sidebar .dropdown-toggle::after {
    bottom: 10px;
    right: 50%;
    -webkit-transform: translateX(50%);
    -ms-transform: translateX(50%);
    transform: translateX(50%);
  }
  
  #sidebar .sidebar-header {
    background: rgb(245, 245, 245);
  }
  
  #sidebar .sidebar-header strong {
    display: none;
    font-size: 1.8em;
  }
  
  #sidebar ul.components {
    border-bottom: 1px dotted gray;
  }
  
  #sidebar ul li a {
    padding-top: 10px;
    font-size: 1.1em;
    display: block;
    padding-bottom: 25px;
  }
  
  #sidebar ul li .active {
    color: rgb(227, 227, 227);
    background: gray;
  }
  
  #sidebar ul li .active .sidebar-icon,
  a:hover .sidebar-icon {
    color: rgb(227, 227, 227);
  }
  
  #sidebar ul li.active>a,
  a[aria-expanded="true"] {
    color: #fff;
    background: rgb(245, 245, 245);
  }
  
  a[data-toggle="collapse"] {
    position: relative;
  }
  
  /* .dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
  } */
  
  ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: rgb(227, 227, 227);
  }
  
  ul li a .sidebar-icon {
    color: gray;
  }
  
  ul.CTAs {
    padding: 20px;
  }
  
  ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
  }
  
  a.download {
    background: #fff;
    color: rgb(245, 245, 245);
  }
  
  a.article,
  a.article:hover {
    background: rgb(42, 87, 131) !important;
    color: #fff !important;
  }
  
  /* ---------------------------------------------------
    CONTENT STYLE
  ----------------------------------------------------- */
  
  #content {
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s;
  }
  
  /* ---------------------------------------------------
    MEDIAQUERIES
  ----------------------------------------------------- */
  
  @media (max-width: 768px) {
    #sidebar {
      text-align: center;
      margin-left: -80px !important;
    }
  
    .dropdown-toggle::after {
      top: auto;
      bottom: 10px;
      right: 50%;
      -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
      transform: translateX(50%);
    }
  
    #sidebar.active {
      margin-left: 0 !important;
    }
  
    #sidebar .sidebar-header h3,
    #sidebar .CTAs {
      display: none;
    }
  
    #sidebar .sidebar-header strong {
      display: block;
    }
  
    #sidebar ul li a {
      padding: 20px 10px;
    }
  
    #sidebar ul li a span {
      font-size: 0.85em;
    }
  
    #sidebar ul li a .sidebar-icon {
      margin-right: 0;
      display: block;
    }
  
    #sidebar ul ul a {
      padding: 10px !important;
    }
  
    #sidebar ul li a .sidebar-icon {
      font-size: 1.3em;
    }
  
    #sidebar {
      margin-left: 0;
    }
  
    #sidebarCollapse span {
      display: none;
    }
  }
  